@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.header {
    margin-bottom: 2em;
}

.header h1 {
    text-transform: uppercase;
    font-family: 'Baloo Tammudu 2', sans-serif;
    font-size: 3em;
    line-height: 2em;
}

.header span {
    color: rgb(66, 255, 224);
}

button {
    background-color: rgb(66, 255, 224);
    color: black;
    font-family: inherit;
    font-size: 1em;
    padding: 0.4em 0.6em;
    margin-bottom: 1em;
    border: none;
    border-radius: 5px;
    transition: all 0.5s;
}

button:hover {
    background-color: rgb(66, 201, 255);
}

@media only screen and (max-width: 600px) {
    button {
        width: 60%;
        align-self: center;
        height: 2.5em;
    }
}
.input-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

input {
    background: inherit;
    color: inherit;
    font-family: inherit;
    border: 2px solid rgb(66, 255, 224, 0.3);
    border-radius: 5px;
    padding: 0.5em;
    margin-bottom: 1em;
    width: 70%;
    height: 3em;
}


@media only screen and (max-width: 600px) {
    .input-wrapper {
        flex-direction: column;
    }

    input {
        width: 100%;
    }
}
.output-area {
    height: 5em;
    padding: 0.5em;
    border-radius: 5px;
    margin-top: 0.5em;
}

.error-msg {
    background: rgba(255, 0, 0, 0.233);
    background: linear-gradient(0deg, rgba(255,0,0,0.23) 0%, rgba(255,255,255,0) 100%);
    padding: 0.5em 1em;
    height: 3em;
}

.shortened-link {
    font-size: 0.9em;
}
.main-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.main {
    margin: 2rem;
    padding: 1.5rem;
    min-height: 35vh;
    background: rgb(0, 0, 0, 0.5);
    border-radius: 10px;
    max-width: 700px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

footer {
    background-color:  #000a0a;
    position: fixed;
    bottom: 0;
    width: 100vw;
}

.footer-icon {
    margin: 0 0.5em;
}

footer a {
    color:  rgb(66, 255, 224, 0.5);
    transition: all 0.5s;
}

footer a:hover {
    color:  rgb(66, 255, 224)
}
.app {
    font-family: 'Comfortaa', sans-serif;
    height: 100vh;
    background-image: linear-gradient(to left top, #001111, #011616, #021a19, #021d1d, #002121);
    background-color: rgb(0, 17, 17);
    text-align: center;
    color: rgb(224, 253, 253);
    line-height: 1.8em;
}
